.menu {
  box-sizing: border-box;

  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    color: #1d1d1d;
    text-decoration: none;
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid #1d1d1d;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: #1d1d1d;
    text-decoration: none;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__top {
    margin-bottom: 24px;

    @include on-tablet {
      margin-bottom: 0;
    }

    position: sticky;
    top: 0;
    z-index: 1;
  }

  overflow: auto;
}
